.container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  
  .card-1, .card-2, .card-3, .card-4, .card-5, .card-6, .card-7, .card-8 {
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.2s ease-in-out;
  }
  
  .card-1:hover, .card-2:hover, .card-3:hover, .card-4:hover, .card-5:hover, .card-6:hover, .card-7:hover, .card-8:hover {
    transform: scale(1.05);
  }
  
  .card-1 img, .card-2 img, .card-3 img, .card-4 img, .card-5 img, .card-6 img , .card-7 img, .card-8 img{
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .card-1 p, .card-2 p, .card-3 p, .card-4 p, .card-5 p, .card-6 p, .card-7 p , .card-8 p{
    padding: 15px;
  }
  
  .card-1 h3, .card-2 h3, .card-3 h3, .card-4 h3, .card-5 h3, .card-6 h3,.card-7 h3,.card-8 h3 {
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .card-1 h4, .card-2 h4, .card-3 h4, .card-4 h4, .card-5 h4, .card-6 h4 , .card-7 h4, .card-8 h4{
    font-size: 1rem;
    color: #555;
  }
  
  button {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  @media (max-width: 768px) {
    .container1 {
      grid-template-columns: 1fr;
    }
  }
  